html,
body {
    height: 100%;
    margin: 0;
}

#root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}


.introductionWrapper::-webkit-scrollbar {
    width: 0;
    height: 0;
}

.introductionWrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    min-height: 250px;
    max-height: 400px;
    padding: auto;
    border-color: blueviolet;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    overflow: auto;
    padding-top: 20px;
    max-width: 1200px;
    flex-wrap: wrap;
}

.introductionSection {
    width: 100%;
    max-width: 200;
    padding: 20px;
    box-sizing: border-box;
}

.contentHeader {
    text-align: center;
    margin-bottom: 20px;
}

.introductionText {
    width: 100%;
}

.textImageContainer {
    display: flex;
    align-items: center;
    /* Center items vertically */
    justify-content: space-between;
    /* Space between text and image */
    flex-direction: row;
    /* Align content in a row */
}

.textContainer {
    flex: 1;
    /* Allow the text container to take up available space */
    margin-left: 20px;
    /* Add some space between the image and the text */
}

.introductionImage {
    width: 100%;
    /* Adjust the width as needed */
    max-width: 200px;
    height: auto;
}

@media (max-width: 800px) {
    .textImageContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .textContainer {
        margin-left: 0;
        margin-top: 20px;
    }

    .introductionImage {
        width: 100px;
    }

    .introductionWrapper {
        flex: 1;
        padding-top: 70px;
        padding-bottom: 20px;
        height: auto;
    }

    .introductionSection,
    .introductionImage {
        max-width: 100%;
        /* Make sections full width on smaller screens */
    }
}